let lang = navigator.language ? navigator.language.split("-")[0] : 'en';
// lang = 'en';
const cnTextMap = {
    'xAssistant': 'X',
    'feature': '',
    'chat': '',
    'send': '',
    'talkMe': '',
    'helloText': '',
    'ok': '',
    'thinking': '',
    'deleteAll': '',
    'dayStrs': ['', '', '', '', '', '', ''],
    'focusthing': "",
    'habit': "",
    'left': "",
    'day': "",
    'hour': "",
    'emptyHit': "",
};
const enTextMap = {
    'xAssistant': 'XAssistant',
    'feature': 'Feature',
    'chat': 'Chat',
    'send': 'Send',
    'talkMe': 'Talk Me',
    'helloText': 'Hello, What can i do for you',
    'ok': 'OK',
    'thinking': 'Thinking',
    'deleteAll': 'Delete All',
    'dayStrs': ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
    'focusthing': "Focus Thing",
    'habit': "Habit",
    'left': "LEFT",
    'day': "D",
    'hour': "H",
    'emptyHit': "EMPTY",
};
export const i18nUtil = {
    getText(code) {
        let res = '';
        switch (lang) {
            case 'zh': res = cnTextMap[code];
                break;
            case 'en': res = enTextMap[code];
                break;
            default: res = cnTextMap[code];
        }
        return res;
    }
}