import { GlobalOutline } from "antd-mobile-icons";
import { useEffect, useState } from "react";
import { i18nUtil } from "../../../../assets/js/common/i18nUtil";
import { commonUtil } from "../../../../assets/js/common/commonUtil";

function EnvironmentFeature(props) {
    const [helloText, setHelloText] = useState('');
    const [nowTimeInfo, setNowTimeInfo] = useState('');
    const [nowTempInfo, setNowTempInfo] = useState('');
    const [nowLocationInfo, setNowLocationInfo] = useState('');
    function getHelloText() {
        const hour = new Date().getHours();
        if (hour < 5) {
            return '';
        } else if (hour < 7) {
            return '';
        } else if (hour < 9) {
            return '';
        } else if (hour < 12) {
            return '';
        } else if (hour < 14) {
            return '';
        } else if (hour < 17) {
            return '';
        } else if (hour < 20) {
            return '';
        } else if (hour < 24) {
            return '';
        }
    }
    function getNowTimeInfo() {
        let nowDate = new Date();
        const dayStrs = i18nUtil.getText('dayStrs');
        return {
            date: nowDate.getFullYear() + '' + (nowDate.getMonth() + 1) + '' + nowDate.getDate() + '',
            chinaDate: commonUtil.getChinaDate(),
            time: nowDate.getHours() + ':' + (nowDate.getMinutes() > 9 ? nowDate.getMinutes() : '0' + nowDate.getMinutes()),
            day: dayStrs[nowDate.getDay()],
        };
    }
    function getNowTempInfo() {
        return ' 1216';
    }
    function getNowLocationInfo() {
        return {
            info: '',
            infoStatus: <GlobalOutline />
        };
    }
    function initData() {
        setHelloText(getHelloText());
        setNowTimeInfo(getNowTimeInfo());
        setNowTempInfo(getNowTempInfo());
        setNowLocationInfo(getNowLocationInfo());
    }
    useEffect(function () {
        initData()
        setInterval(() => {
            initData();
        }, 1000 * 60);
        // eslint-disable-next-line
    }, []);
    return (
        <div className="feature-item">
            <div className="feature-item-row">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="env-hello-text">{helloText}</div>
                        <div className="env-temp-text">{nowTempInfo}</div>
                    </div>
                    <div className="feature-head-right">
                        <div className="env-date">
                            <div>{nowTimeInfo.date}</div>
                            <div className="flex-center">{nowTimeInfo.chinaDate}</div>
                        </div>
                    </div>
                </div>
                <div className="feature-content">
                    <div className="env-time-info">
                        <div className="env-time">{nowTimeInfo.time}</div>
                        <div className="env-day">{nowTimeInfo.day}</div>
                    </div>
                    <div className="env-location-info">
                        <div className="env-location">{nowLocationInfo.info}</div>
                        <div className="env-location-status flex-center" style={{ color: 'lightgreen' }}>{nowLocationInfo.infoStatus}</div>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default EnvironmentFeature;