import "../../../../assets/css/body/hand.css"
import EnvironmentFeature from "./EnvironmentFeature";
import FocusthingFeature from "./FocusthingFeature";
import HabitFeature from "./HabitFeature";
function FeaturePage(props) {
    return (
        <div className="feature-list" style={{ display: props.show ? undefined : 'none' }}>
            <EnvironmentFeature />
            <HabitFeature habitList={props.habitList} setHabitList={(value) => {
                props.setHabitList(value);
            }} loadHabitList={() => props.loadHabitList()} />
            <FocusthingFeature eventList={props.eventList} loadEventList={() => {
                props.loadEventList()
            }}/>
        </div>
    );
}
export default FeaturePage;