import { commonUtil } from "../common/commonUtil";
const textHandleStrategys = {
    "(.*?)()(.*)": {
        analysis: (userMsg) => {
            let res = {};
            if (!userMsg) {
                return undefined;
            }
            // 1.
            const remidRegex = new RegExp(/^(.*?)()(.*)$/);
            const matchResult = userMsg.match(remidRegex);
            const leftTextStr = matchResult[1].trim();
            const rightTextStr = matchResult[3].trim();
            res.title = rightTextStr;
            // 2.
            const nowDate = new Date();
            let nowDay = nowDate.getDay();
            if (nowDay === 0) {
                nowDay = 7;
            }
            const hourTimerRegex = new RegExp(/^(\d+)()$/);
            const minuteTimerRegex = new RegExp(/^(\d+)()$/);
            const secondTimerRegex = new RegExp(/^(\d+)()$/);
            const dayTimerRegex = new RegExp(/^(\d{4}||)(\d{1,2})(\d{1,2})(\d{1,2})(\d{0,2})/);
            const repeatDayTimerRegex = new RegExp(/^(||||||)(\d{1,2})(\d{0,2})/);
            const hourTimerRegexMatch = leftTextStr.match(hourTimerRegex);
            const minuteTimerRegexMatch = leftTextStr.match(minuteTimerRegex);
            const secondTimerRegexMatch = leftTextStr.match(secondTimerRegex);
            const dayTimerRegexMatch = leftTextStr.match(dayTimerRegex);
            const repeatDayTimerRegexMatch = leftTextStr.match(repeatDayTimerRegex);
            if (hourTimerRegexMatch && hourTimerRegexMatch.length > 0) {
                const hourValue = parseInt(hourTimerRegexMatch[1]);
                let todoDate = new Date(nowDate.getTime() + hourValue * 60 * 60 * 1000);
                res.timestamp = todoDate.getTime();
                res.timeDesc = todoDate.toLocaleString();
                res.timeStr = todoDate.toLocaleTimeString();
                res.type = 'hTimer';
            }
            else if (minuteTimerRegexMatch && minuteTimerRegexMatch.length > 0) {
                const minuteValue = minuteTimerRegexMatch[1];
                let todoDate = new Date(nowDate.getTime() + minuteValue * 60 * 1000);
                res.timestamp = todoDate.getTime();
                res.timeDesc = todoDate.toLocaleString();
                res.timeStr = todoDate.toLocaleTimeString();
                res.type = 'mTimer';
            }
            else if (secondTimerRegexMatch && secondTimerRegexMatch.length > 0) {
                const secondValue = secondTimerRegexMatch[1];
                let todoDate = new Date(nowDate.getTime() + secondValue * 1000);
                res.timestamp = todoDate.getTime();
                res.timeDesc = todoDate.toLocaleString();
                res.timeStr = todoDate.toLocaleTimeString();
                res.type = 'sTimer';
            }
            else if (dayTimerRegexMatch && dayTimerRegexMatch.length > 0) {
                let yearValue = parseInt(dayTimerRegexMatch[1]);
                if (dayTimerRegexMatch[1] === '') {
                    yearValue = new Date().getFullYear();
                }
                if (dayTimerRegexMatch[1] === '') {
                    yearValue = '';
                }
                const monthValue = parseInt(dayTimerRegexMatch[2]);
                const dateValue = parseInt(dayTimerRegexMatch[3]);
                const hourValue = parseInt(dayTimerRegexMatch[4]);
                let minuteValue = parseInt(dayTimerRegexMatch[5]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                res.date = {
                    month: commonUtil.formatTimeNumber(monthValue),
                    date: commonUtil.formatTimeNumber(dateValue),
                    hour: commonUtil.formatTimeNumber(hourValue),
                    minute: commonUtil.formatTimeNumber(minuteValue),
                };
                res.timeStr = res.date.hour + ":" + res.date.minute;
                if (yearValue === '') {
                    res.repeate = true;
                    res.timeDesc = "" + res.date.month + "" + res.date.date + " " + res.date.hour + ":" + res.date.minute;
                } else {
                    const todoDate = new Date(yearValue, monthValue - 1, dateValue, hourValue, minuteValue, 0)
                    res.timestamp = todoDate.getTime();
                    res.timeDesc = todoDate.toLocaleString();
                }
                res.type = 'dTimer';
            }
            else if (repeatDayTimerRegexMatch && repeatDayTimerRegexMatch.length > 0) {
                const todoHour = parseInt(repeatDayTimerRegexMatch[2]);
                let todoMinute = parseInt(repeatDayTimerRegexMatch[3]);
                if (!todoMinute || todoMinute === '') {
                    todoMinute = 0;
                }
                let todoHourStr = commonUtil.formatTimeNumber(todoHour);
                let todoMinuteStr = commonUtil.formatTimeNumber(todoMinute);
                switch (repeatDayTimerRegexMatch[1]) {
                    case '':
                        res.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), todoHour, todoMinute, 0).getTime();
                        res.timeDesc = "" + todoHourStr + ":" + todoMinuteStr;
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '':
                        const tomorrowDate = new Date(nowDate.getTime() + 24 * 60 * 60 * 1000);
                        res.timestamp = new Date(tomorrowDate.getFullYear(), tomorrowDate.getMonth(), tomorrowDate.getDate(), todoHour, todoMinute, 0).getTime();
                        res.timeDesc = "" + todoHourStr + ":" + todoMinuteStr;
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '':
                        res.repeatType = 'everyday';
                        res.timeDesc = "" + todoHourStr + ":" + todoMinuteStr;
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '':
                        res.repeatType = 'workday';
                        res.timeDesc = "" + todoHourStr + ":" + todoMinuteStr;
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '':
                        res.repeatType = 'restday';
                        res.timeDesc = "" + todoHourStr + ":" + todoMinuteStr;
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '':
                        res.repeatType = 'sat';
                        res.timeDesc = "" + todoHourStr + ":" + todoMinuteStr;
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '':
                        res.repeatType = 'sun';
                        res.timeDesc = "" + todoHourStr + ":" + todoMinuteStr;
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    default: ;
                }
                res.type = 'repeatTimer';
            }
            else {
                return undefined;
            }
            return res;
        },
        handle: (analysisRes) => {
            // 1. 
            brainStoreUtil.add(brainStoreType.TODO_LIST, analysisRes);
            // 2. 
            return "" + analysisRes.timeDesc + "" + analysisRes.title;
        }
    },
    "(.*?)(.*?)": {
        analysis: (userMsg) => {
            let res = {};
            if (!userMsg) {
                return undefined;
            }
            // 1.
            const eventRegex = new RegExp(/^(.*?)(.*)$/);
            const eventRegexMatch = userMsg.match(eventRegex);
            const eventTimeOption = eventRegexMatch[1].trim();
            const eventTitle = eventRegexMatch[2].trim();
            res.title = eventTitle;
            // 2.
            const nowDate = new Date();
            let nowDay = nowDate.getDay();
            if (nowDay === 0) {
                nowDay = 7;
            }
            const yearDayTimerRegex = new RegExp(/^(\d{1,4})(\d{1,2})(\d{1,2})(\d{1,2})(\d{0,2})/);
            const nowYearDayTimerRegex = new RegExp(/^(\d{1,2})(\d{1,2})(\d{1,2})(\d{0,2})/);
            const repeatDayTimerRegex = new RegExp(/^(||)(\d{1,2})(\d{0,2})/);
            const repeatYearTimerRegex = new RegExp(/^(\d{1,2})(\d{1,2})(\d{1,2})(\d{0,2})/);
            const yearDayTimerRegexMatch = eventTimeOption.match(yearDayTimerRegex);
            const nowYearDayTimerRegexMatch = eventTimeOption.match(nowYearDayTimerRegex);
            const repeatDayTimerRegexMatch = eventTimeOption.match(repeatDayTimerRegex);
            const repeatYearTimerRegexMatch = eventTimeOption.match(repeatYearTimerRegex);
            if (yearDayTimerRegexMatch && yearDayTimerRegexMatch.length > 0) {
                const yearValue = parseInt(yearDayTimerRegexMatch[1]);
                const monthValue = parseInt(yearDayTimerRegexMatch[2]);
                const dateValue = parseInt(yearDayTimerRegexMatch[3]);
                let hourValue = parseInt(yearDayTimerRegexMatch[4]);
                let minuteValue = parseInt(yearDayTimerRegexMatch[5]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                const todoDate = new Date(yearValue, monthValue - 1, dateValue, hourValue, minuteValue, 0)
                res.timestamp = todoDate.getTime();
                res.type = 'day';
                res.timeDesc = todoDate.toLocaleString();
            } else if (nowYearDayTimerRegexMatch && nowYearDayTimerRegexMatch.length > 0) {
                const monthValue = parseInt(nowYearDayTimerRegexMatch[1]);
                const dateValue = parseInt(nowYearDayTimerRegexMatch[2]);
                let hourValue = parseInt(nowYearDayTimerRegexMatch[3]);
                let minuteValue = parseInt(nowYearDayTimerRegexMatch[4]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                const todoDate = new Date(new Date().getFullYear(), monthValue - 1, dateValue, hourValue, minuteValue, 0)
                res.timestamp = todoDate.getTime();
                res.type = 'fixed-day';
                res.timeDesc = todoDate.toLocaleString();
            }
            else if (repeatDayTimerRegexMatch && repeatDayTimerRegexMatch.length > 0) {
                const dayType = repeatDayTimerRegexMatch[1];
                const hourValue = parseInt(repeatDayTimerRegexMatch[2]);
                let minuteValue = parseInt(repeatDayTimerRegexMatch[3]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                res.date = {
                    hour: commonUtil.formatTimeNumber(hourValue),
                    minute: commonUtil.formatTimeNumber(minuteValue),
                };
                if (dayType === '') {
                    res.type = 'every-day';
                    res.timeDesc = "" + res.date.hour + ":" + res.date.minute;
                }
                if (dayType === '') {
                    res.type = 'work-day';
                    res.timeDesc = "" + res.date.hour + ":" + res.date.minute;
                }
            } else if (repeatYearTimerRegexMatch && repeatYearTimerRegexMatch.length > 0) {
                const monthValue = parseInt(repeatYearTimerRegexMatch[1]);
                const dateValue = parseInt(repeatYearTimerRegexMatch[2]);
                const hourValue = parseInt(repeatYearTimerRegexMatch[3]);
                let minuteValue = parseInt(repeatYearTimerRegexMatch[4]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                res.date = {
                    month: commonUtil.formatTimeNumber(monthValue),
                    date: commonUtil.formatTimeNumber(dateValue),
                    hour: commonUtil.formatTimeNumber(hourValue),
                    minute: commonUtil.formatTimeNumber(minuteValue),
                };
                res.type = 'every-year';
                res.timeDesc = "" + res.date.month + "" + res.date.date + "" + res.date.hour + ":" + res.date.minute;
            }
            else {
                return undefined;
            }
            return res;
        },
        handle: (analysisRes) => {
            // 1. 
            brainStoreUtil.add(brainStoreType.FOCUSTHING_LIST, analysisRes);
            // 2. 
            return "" + analysisRes.timeDesc + "" + analysisRes.title + "";
        }
    },
    "(.*)(.*)": {
        analysis: (userMsg) => {
            const setRegExp = new RegExp("^(.*)(.*)$");
            const setRegExpMatch = userMsg.match(setRegExp);
            let analysisRes = {}
            analysisRes.setTypeName = setRegExpMatch[1];
            analysisRes.setTypeValue = setRegExpMatch[2];
            switch (analysisRes.setTypeName) {
                case '':
                    analysisRes.setType = brainStoreType.GPT_API_KEY;
                    break;
                case '':
                    analysisRes.setType = brainStoreType.ASSISTANT_LOGO;
                    break;
                case '':
                    analysisRes.setType = brainStoreType.TODO_LIST;
                    break;
                default: ;
            }
            return analysisRes;
        },
        handle: (analysisRes) => {
            if (!analysisRes.setType) {
                return "" + analysisRes.setTypeName + "";
            }
            if (analysisRes.setTypeValue && analysisRes.setTypeValue.trim() !== '') {
                localStorage.setItem(analysisRes.setType, analysisRes.setTypeValue);
                if (analysisRes.reload) {
                    setTimeout(() => {
                        window.location.reload();
                    }, 1000);
                }
                return "" + analysisRes.setTypeName + "" + analysisRes.setTypeValue + (analysisRes.reload ? "," : "");
            } else {
                return "" + analysisRes.setTypeName + (analysisRes.reload);
            }
        },
    },
    "(.*)": {
        analysis: (userMsg) => {
            const setRegExp = new RegExp("^(.*)$");
            const setRegExpMatch = userMsg.match(setRegExp);
            let analysisRes = {}
            analysisRes.setTypeName = setRegExpMatch[1];
            switch (analysisRes.setTypeName) {
                case '':
                    analysisRes.setType = brainStoreType.ASSISTANT_LOGO;
                    break;
                default: ;
            }
            return analysisRes;
        },
        handle: (analysisRes) => {
            if (!analysisRes.setType) {
                return "" + analysisRes.setTypeName + "";
            }
            if (analysisRes) {
                localStorage.removeItem(analysisRes.setType);
                if (analysisRes.reload) {
                    setTimeout(() => {
                        window.location.reload();
                    }, 1000);
                }
                return "" + (analysisRes.reload ? "," : "");
            }
        },
    },
    "": {
        analysis: (userMsg) => {
            let analysisRes = {}
            analysisRes.typeList = [brainStoreType.TODO_LIST];
            return analysisRes;
        },
        handle: (analysisRes) => {
            let appDataJsonObj = {};
            let appDataJsonFileName = "XAssistantData_" + new Date().getTime();
            analysisRes.typeList.forEach(type => {
                appDataJsonObj[type] = brainStoreUtil.getDataList(type);
            });
            if (analysisRes) {
                commonUtil.downloadJsonFile(appDataJsonObj, appDataJsonFileName);
            }
            return "" + appDataJsonFileName;
        },
    },
}
export const brainStoreType = {
    CHAT_LIST: 'chatList',
    TODO_LIST: 'todoList',
    FOCUSTHING_LIST: ' focusthingList',
    GPT_API_KEY: 'gptApiKey',
    ASSISTANT_LOGO: 'assistant-logo',
}
export const brainStoreUtil = {
    add(dataType, data) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        data.dataId = new Date().getTime();
        data.createTime = new Date().getTime();
        dataList.push(data);
        localStorage.setItem(dataType, JSON.stringify(dataList));
        return data.dataId;
    },
    delById(dataType, dataId) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        dataList = dataList.filter(data => data.dataId !== dataId);
        localStorage.setItem(dataType, JSON.stringify(dataList));
        return true;
    },
    delAll(dataType) {
        localStorage.removeItem(dataType);
    },
    updateById(dataType, newData) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        dataList = dataList.filter(data => data.dataId !== newData.dataId);
        newData.updateTime = new Date().getTime();
        dataList.push(newData);
        localStorage.setItem(dataType, JSON.stringify(dataList));
        return true;
    },
    getDataList(dataType) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        dataList = commonUtil.sortArray(dataList, 'createTime');
        return dataList;
    },
    getById(dataType, dataId) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        const dataList = JSON.parse(dataListStr);
        const data = dataList.filter(item => item.dataId === dataId)[0];
        return data;
    },
}
export const brainThinkUtil = {
    localHandleText(text) {
        // 1.
        const textHandleStrategyParterns = Object.keys(textHandleStrategys);
        // 2.
        let textHandleStrategy = undefined;
        textHandleStrategyParterns.forEach(partern => {
            const textMatchRes = text.match(partern);
            if (textMatchRes && textMatchRes.length > 0) {
                textHandleStrategy = textHandleStrategys[partern];
            }
        });
        if (!textHandleStrategy) {
            return undefined;
        }
        // 3.
        const analysisRes = textHandleStrategy.analysis(text);
        if (!analysisRes) {
            return undefined;
        }
        // 4.
        const calculateRes = textHandleStrategy.handle(analysisRes);
        if (!calculateRes) {
            return undefined;
        }
        return calculateRes;
    },
    async netHandleText(message) {
        let netHandleTextRes = await this.getAliYunRes(message);
        return netHandleTextRes;
    },
    async getAliYunRes(message) {
        let aiApi = 'https://dashscope.aliyuncs.com/compatible-mode/v1/chat/completions';
        let aiApiKey = localStorage.getItem(brainStoreType.GPT_API_KEY);
        let aiApiHeader = {
            "Authorization": aiApiKey,
            "Content-Type": "application/json",
        };
        let aiApiReqBody = {
            "model": "qwen-plus",
            "messages": [
                {
                    "role": "system",
                    "content": "You are a helpful assistant."
                },
                {
                    "role": "user",
                    "content": message,
                }
            ]
        };
        if (!aiApiKey || !aiApi || !aiApiHeader || !aiApiReqBody) {
            return undefined;
        }
        return fetch(aiApi, {
            method: 'POST',
            headers: aiApiHeader,
            body: JSON.stringify(aiApiReqBody)
        }).then(response => response.json()).then(data => data.choices[0].message.content).catch(error => {
            console.log(error);
        });
    },
    handleAlertMsgOperate(alertMsg) {
        if (!alertMsg.dataType) {
            return;
        }
        const alertMsgData = brainStoreUtil.getById(alertMsg.dataType, alertMsg.dataTypeDataId);
        if (!alertMsgData) {
            return;
        }
        if (alertMsgData.ok === true) {
            alertMsgData.ok = false;
            alertMsg.isOk = false;
        } else if (alertMsgData.okDates && alertMsgData.okDates.indexOf(new Date().toLocaleDateString()) >= 0) {
            alertMsgData.okDates = alertMsgData.okDates.filter(date => date !== new Date().toLocaleDateString());
            alertMsg.isOk = false;
        } else {
            if (alertMsgData.repeatType) {
                let okDates = alertMsgData.okDates;
                if (!okDates) {
                    okDates = [];
                }
                okDates.push(new Date(alertMsg.createTime).toLocaleDateString());
                alertMsgData.okDates = okDates;
                alertMsg.isOk = true;
            } else {
                alertMsgData.ok = true;
                alertMsg.isOk = true;
            }
        }
        brainStoreUtil.updateById(brainStoreType.CHAT_LIST, alertMsg);
        // 
        brainStoreUtil.updateById(alertMsg.dataType, alertMsgData);
    },
}