import { i18nUtil } from "../../../../assets/js/common/i18nUtil";
import { brainStoreType, brainStoreUtil } from "../../../../assets/js/head/brainUtil";
import TimeCountDown from "./TimeCountDown";

function FocusthingFeature(props) {
    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="feature-head-title">{i18nUtil.getText('focusthing')}</div>
                    </div>
                    <div className="feature-head-right">

                    </div>
                </div>
                <div className="feature-content">
                    <div className="focusthing-item-list">
                        {
                            props.eventList.map(item => (
                                <div className="focusthing-item" key={item.id} onDoubleClick={() => {
                                    if(item.id === "holiday") {
                                        return
                                    }
                                    if (window.confirm('' + item.title + i18nUtil.getText('focusthing'))) {
                                        brainStoreUtil.delById(brainStoreType.FOCUSTHING_LIST, item.id);
                                        props.loadEventList();
                                    }
                                }}>
                                    <div className="focusthing-item-head">
                                        <div className="focusthing-type">{item.titleIcon}</div>
                                        <div className="focusthing-time-info">
                                            <div className="focusthing-date">{item.date}</div>
                                            <div className="focusthing-time">{item.time}</div>
                                        </div>
                                    </div>
                                    <div className="focusthing-item-content">
                                        <div className="focusthing-title">{item.title}</div>
                                        <div className="focusthing-left-info">
                                            <div className="focusthing-left-day">
                                                <TimeCountDown timestamp={item.timestamp} />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                        <div style={{ display: props.eventList.length === 0 ? undefined : 'none' }} className="chat-record-list">
                            <div className="sys-empty">{i18nUtil.getText('emptyHit')}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default FocusthingFeature;